/* post navigator*/
.post-nav {
  overflow: hidden;
  margin-top: 15px;
  margin-bottom: 20px;
  padding: 10px;
  white-space: nowrap;
  border-top: 1px solid #eee;
}
.post-nav a {
  display: inline-block;
  line-height: 25px;
  font-size: 15px;
  color: #555;
  border-bottom: none;
  float: left;
}
.post-nav a.pre {
  float: left;
}
.post-nav a.pre:before {
  font-family: "FontAwesome";
  content: "\F0D9";
  padding-right: 0.3em;
}
.post-nav a.next {
  float: right;
}
.post-nav a.next:after {
  font-family: "FontAwesome";
  content: "\F0DA";
  padding-left: 0.3em;
}
.post-nav a:hover {
  border-bottom: none;
  color: #222;
}
/* toc*/
.toc-article {
  border: 1px solid #bbb;
  border-radius: 7px;
  margin: 1.1em 0 0 2em;
  padding: 0.7em 0.7em 0 0.7em;
  max-width: 40%;
