  line-height: 1em;
  float: right;
}
#toc .toc {
  padding: 0;
  margin: 0.5em;
  line-height: 1.8em;
}
#toc .toc li {
  list-style-type: none;
}
#toc .toc-child {
  margin-left: 1em;
  padding-left: 0;
}
